export default{
	//
	dateTime(e){
		let old = new Date(e)
		let now = new Date()
		//old
		let d = old.getTime();
		let h = old.getHours();
		let m = old.getMinutes();
		let Y = old.getFullYear();
		let M = old.getMonth()+1;
		let D = old.getDate();
		//now
		let nd = now.getTime();
		let nh = now.getHours();
		let n = now.getMinutes();
		let nY = now.getFullYear();
		let nM = now.getMonth()+1;
		let nD = now.getDate();
		
		if(D === nD && Y === nY && M === nM){
			//
			if(h<10){
				h = '0'+h;
			}
			if(m<10){
				m = '0'+m;
			}
			return  h+':'+m;
		}
		if(D+1 === nD && Y === nY && M === nM){
			//
			if(h<10){
				h = '0'+h;
			}
			if(m<10){
				m = '0'+m;
			}
			return  ' ' +h+':'+m;
		}else{
			return Y+'/'+M+'/'+D
		}
	},
	//
	dateTime1(e){
		let old = new Date(e)
		let now = new Date()
		//old
		let d = old.getTime();
		let h = old.getHours();
		let m = old.getMinutes();
		let Y = old.getFullYear();
		let M = old.getMonth()+1;
		let D = old.getDate();
		//now
		let nd = now.getTime();
		let nh = now.getHours();
		let n = now.getMinutes();
		let nY = now.getFullYear();
		let nM = now.getMonth()+1;
		let nD = now.getDate();
		
		if(D === nD && Y === nY && M === nM){
			//
			if(h<10){
				h = '0'+h;
			}
			if(m<10){
				m = '0'+m;
			}
			return  h+':'+m;
		}
		if(D+1 === nD && Y === nY && M === nM){
			//
			if(h<10){
				h = '0'+h;
			}
			if(m<10){
				m = '0'+m;
			}
			return  ' ' +h+':'+m;
		}else if(Y===nY){
			if(h<10){
				h = '0' + h
			}
			if(m<10){
				m = '0'+m
			}
			return M+''+D+'  ' +h+':'+m; 
		}else{
			if(h<10){
				h = '0' + h
			}
			if(m<10){
				m = '0'+m
			}
			return Y+''+M+''+D+'  ' +h+':'+m; 
		}
	},
	//
	detailTime1(e){
		let old = new Date(e);
		let Y = old.getFullYear();
		let M = old.getMonth();
		let D = old.getDate();
		if(M<10){
			M = '0'+M
		}
		if(D<10){
			D = '0' +D
		}
		return Y+'-'+M+'-'+D
	},
	//
	spaceTime(old,now){
		old = new Date(old);
		now = new Date(now);
		var told = old.getTime();
		var tnow = now.getTime();
		if(told >(tnow+1000*60*5)){
			return now;
		}else{
			return ''
		}
	},
	// 
	debounce(fn,t){
		let delay = t || 500;
		let timer;
		return function() {
			let args = arguments;
			if(timer){
				clearTimeout(timer);
			}
			timer = setTimeout(() => {
				timer = null;
				fn.apply(this,args);
			},delay)
		}
	}
}